import React, { useEffect, useState } from "react";
import axios from "../../axios";
import {
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Table,
  InputLabel,
  MenuItem,
  FormControl,
  Select,
  Stack,
  Button,
  LinearProgress,
  Grid,
  AlertTitle,
  Alert,
  Snackbar,
} from "@mui/material";
import { TextField } from "formik-material-ui";
import { Formik, Form, Field, ErrorMessage } from "formik";
import AddBoxOutlinedIcon from "@mui/icons-material/AddBoxOutlined";
import { useSelector } from "react-redux";
import * as Yup from "yup";
var generator = require("generate-password");
const phoneRegExp =
  /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const userRegExp = /^[a-zA-Z0-9]+$/;
function CreateDeptUser() {
  const collegeID = useSelector((state) => state.collegeID);
  const [deptList, setdeptList] = useState([]);
  const [userList, setUserList] = useState([]);
  const [deptID, setdeptID] = useState([]);
  const [facultyList, setFacultytList] = useState([]);
  const [facultyId, setFacultyId] = useState("");
  const [password, setPassword] = useState("");
  const [open, setOpen] = React.useState(false);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };
  const handleClick = (id, username) => {
    var genearted_password = generator.generate({
      length: 10,
      numbers: true,
    });
    setPassword(genearted_password);
    const values = {
      username: username,
      id: id,
      newPassword: genearted_password,
      confirmPassword: genearted_password,
    };
    axios
      .post("login/Passwordchange", JSON.stringify(values), {
        headers: {
          // Overwrite Axios's automatically set Content-Type
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      })
      .then((res) => {
        if (res.status === 200 && res.data.flag === "Success") {
          setOpen(true);
        }
      })
      .catch(function (error) {
        alert("Error occured:" + error);
      });
  };

  const handleChange = (event) => {
    setFacultyId(event.target.value);
    axios
      .get("api/departments/" + event.target.value + "/" + collegeID, {})
      .then((res) => {
        setdeptList(res.data);
      })
      .catch((error) => {
        alert(error);
        console.log(error);
      });
  };

  const handleDeptChange = (e) => {
    setdeptID(e.target.value);
    axios
      .get("login/userlist/" + e.target.value, {})
      .then((res) => {
        setUserList(res.data);
      })
      .catch((error) => {
        alert(error);
        console.log(error);
      });
  };
  const handleChangeUserName = (e) => {
    let username = e.target.value;
    if (username.toString().length > 3) {
      axios
        .post(
          "login/checkusername",
          { username: username },
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
              Accept: "application/json",
            },
          }
        )
        .then((res) => {
          if (res.status === 200 && res.data.error === "Failed") {
            alert("User already exists");
          } else if (res.status === 200 && res.data.error === "Success") {
            alert("User Name avaliable");
          }
        })
        .catch(function (error) {
          alert("Error occured:" + error);
        });
    }
  };
  useEffect(() => {
    axios
      .get("api/departments/faculty/" + collegeID)
      .then((res) => {
        if (res.status === 200) {
          setFacultytList(res.data);
        }
      })
      .catch(function (error) {
        alert("Error occured:" + error);
      });
  }, []);

  return (
    <Grid container spacing={2}>
      <Grid item lg={6} xs={12} sm={12} md={12}>
        <Stack sx={{ width: "80%" }} spacing={2}>
          <h4>
            <AddBoxOutlinedIcon className="mr-2" />
            {"Select Faculty -> Department"}
          </h4>
          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">
              Faculty / School
            </InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={facultyId}
              label="Faculty / School "
              onChange={handleChange}
            >
              {facultyList.map((faculty, index) => (
                <MenuItem key={index} value={faculty.Faculty_ID}>
                  {faculty.Faculty_Name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <FormControl fullWidth>
            <InputLabel id="department-lael">Select Department</InputLabel>
            <Select
              labelId="department-label"
              id="department"
              value={deptID}
              label="Select Department "
              onChange={handleDeptChange}
            >
              {deptList.map((dept, index) => (
                <MenuItem key={index} value={dept.Department_ID}>
                  {dept.Department_Name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Stack>
      </Grid>
      <Grid item lg={6} xs={12} sm={12} md={12}>
        <Stack sx={{ width: "100%" }} spacing={2}>
          <Formik
            initialValues={{
              username: "",
              Dept_ID: "",
              email: "",
              phone_no: "",
              firstName: "",
              collegeID: collegeID,
            }}
            validationSchema={Yup.object({
              username: Yup.string()
                .max(20, "Must be 20 characters or less")
                .min(5, "Must be at least 5 characters")
                .matches(
                  userRegExp,
                  "Username should be alphanumeric only without spaces & special symbols"
                )
                .required("Username required"),
              password: Yup.string()
                .max(20, "Must be 20 characters or less")
                .min(8, "Must be at least 8 characters")
                .required("Password required"),
              firstName: Yup.string()
                .max(50, "Must be 250 characters or less")
                .min(5, "Must be at least 5 characters")
                .required("Full Name Required"),
              email: Yup.string()
                .email()
                .required("Valid Email Of Student Required"),
              phone_no: Yup.string()
                .min(10, "Mobile number is not valid")
                .required("Mobile No Required")
                .matches(phoneRegExp, "Mobile number is not valid"),
            })}
            onSubmit={(values, { setSubmitting, resetForm }) => {
              values.Dept_ID = deptID;
              if (deptID) {
                setTimeout(() => {
                  setSubmitting(false);
                  axios
                    .post(
                      "api/usercreate/" + collegeID,
                      JSON.stringify(values),
                      {
                        headers: {
                          // Overwrite Axios's automatically set Content-Type
                          "Content-Type": "application/json",
                          Accept: "application/json",
                        },
                      }
                    )
                    .then((res) => {
                      if (res.status === 200) {
                        alert("User Created Successfully ");
                      }
                    })
                    .catch(function (error) {
                      alert("Error occured:" + error);
                    });
                  resetForm();
                }, 500);
              } else {
                setOpen(true);
              }
            }}
          >
            {({ submitForm, isSubmitting, errors, touched }) => (
              <Form>
                <Stack spacing={2}>
                  <h4>
                    <AddBoxOutlinedIcon className="mr-2" />
                    Add New User For The Selected Department
                  </h4>
                  <Field
                    component={TextField}
                    name="username"
                    type="text"
                    label="User Name"
                    variant="outlined"
                    fullWidth
                  />
                  <Field
                    component={TextField}
                    name="password"
                    type="password"
                    label="Password"
                    variant="outlined"
                    fullWidth
                  />
                  <Field
                    component={TextField}
                    name="firstName"
                    type="text"
                    label="Full Name"
                    variant="outlined"
                    fullWidth
                  />
                  <Field
                    component={TextField}
                    name="email"
                    type="text"
                    label="Email"
                    variant="outlined"
                    fullWidth
                  />
                  <Field
                    component={TextField}
                    name="phone_no"
                    type="text"
                    label="Mobile No"
                    variant="outlined"
                    fullWidth
                  />
                  {isSubmitting && <LinearProgress />}
                </Stack>
                <Button
                  className="mt-3"
                  variant="contained"
                  color="primary"
                  disabled={isSubmitting}
                  onClick={submitForm}
                >
                  Add Department User
                </Button>
              </Form>
            )}
          </Formik>
        </Stack>
      </Grid>
      <Grid item lg={12} xs={12} sm={12} md={12}>
        <Snackbar
          anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
          open={open}
          autoHideDuration={6000}
          onClose={handleClose}
        >
          <Alert
            onClose={handleClose}
            variant="filled"
            severity="success"
            sx={{ width: "100%" }}
          >
            Password has been Successfully Reset!
          </Alert>
        </Snackbar>
        {password ? (
          <Alert variant="outlined" severity="info">
            <AlertTitle>Password Reset</AlertTitle>
            Password has been reset. The new password is —{" "}
            <strong>{password}</strong>
          </Alert>
        ) : null}
        <TableContainer component={Paper}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell style={{ textTransform: "uppercase" }}>
                  User Name
                </TableCell>
                <TableCell style={{ textTransform: "uppercase" }}>
                  Name
                </TableCell>
                <TableCell style={{ textTransform: "uppercase" }}>
                  Phone
                </TableCell>
                <TableCell style={{ textTransform: "uppercase" }}>
                  Email
                </TableCell>
                <TableCell style={{ textTransform: "uppercase" }}>
                  Password
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {userList.map((row, index) => (
                <TableRow key={index}>
                  <TableCell align="left" style={{ fontWeight: "bold" }}>
                    {row.username}
                  </TableCell>
                  <TableCell align="left">{row.firstName}</TableCell>
                  <TableCell align="left">{row.email}</TableCell>
                  <TableCell align="left">{row.phone_no}</TableCell>
                  <TableCell align="left">
                    <Button
                      variant="contained"
                      onClick={() => handleClick(row.id, row.username)}
                    >
                      Reset
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
    </Grid>
  );
}

export default CreateDeptUser;
